@forward 'global';
@forward 'pages';
@forward 'components';

body {
  background-color: #eee;
  margin: 0;
}

.App {
  display:flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  // background-color: #ccc;
}

.flex-column {
  display:flex;
  flex-direction: column;
}

.force-remove {
  min-width: 0 !important;
  font-size: 0 !important;
  opacity: 0 !important;
}


.eye-button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  transition: 0.3s ease;
}

.eye-button:hover {
  transform: scale(1.5);  
}

.eye-button:active {
  transform: scale(1); 
}



.authentication-modal {
  border-radius: 0.5rem;
  display:flex;
  flex-direction: column;
  max-height: 31rem;
  min-width: 20rem;
  padding: 1.5rem;
  width: fit-content;
  height: fit-content;
  background-color: transparent;
  position:relative;
  box-shadow: 0.31rem 0.31rem 0.93rem -0.06rem rgba(0,0,0,0.75);
  font-family: 'Montserrat';
}

.authentication-modal:after {
  position: absolute;
  content: "";
  right: -0.62rem;
  bottom: 1.13rem;
  width: 0;
  height: 0;
  border-left: 0px solid transparent;
  border-right: 0.63rem solid transparent;
  border-bottom: 0.63rem solid #1a044e;
}

.auth-form {
  display: flex;
  flex-direction: column;
}



/* From uiverse.io by @alexruix */
.auth-input {
  line-height: 1.75rem;
  border: 0.12rem solid transparent;
  border-bottom-color: #777;
  padding: .2rem 0;
  margin: 1rem 0;
  outline: none;
  background-color: transparent;
  color: #0d0c22;
  transition: .3s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 90%;
  box-sizing: border-box;
 }
 
 .auth-input:focus, .auth-input:hover {
  outline: none;
  padding: .2rem 1rem;
  border-radius: 1rem;
  border-color: #7a9cc6;
 }
 
 .auth-input::placeholder {
  color: #777;  
 }
 
 .auth-input:focus::placeholder {
  opacity: 0;
  transition: opacity .3s;
 }
 
 .auth-btn {
  position: absolute;
  border-radius: 1.9rem;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  background-color: #0f0092;
  border-width: thin;
  color: #FFF;
  padding: 0.75rem 1.56rem;
  display: inline-block;
  font-size: 0.75rem;
  font-weight: bold;
  letter-spacing: 0.31rem;
  right: -0.63rem;
  bottom: -1.38rem;
  cursor: pointer;
  transition: all .25s ease;
  box-shadow: -0.31rem 0.37rem 1.25rem 0px rgba(26, 26, 26, 0.4);
}

.auth-btn:hover {
  background-color: #07013d;
  box-shadow: -0.31rem  0.37rem 1.25rem 0px rgba(88, 88, 88, 0.569);
}

.auth-state {
  border:none;
  background-color: transparent;
  margin-top: 2rem;
  font-family: 'Montserrat';
}
.auth-state:hover {
  color: #0f0092;    
  cursor: pointer;
}
 
 
 
.header-container {
  background-color: #4d5f7d;
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  min-height: 6vh;
  height: fit-content;  
  // position:relative;
}

.header-account-btn {  
  padding: 0.5rem;
  background-color: transparent;
  font-family: 'Montserrat';
  font-size: 1rem;  
  color: #fff;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.header-modal{ 
  border-radius: 0.25rem;
  background-color: #7fa0ac;
  color: #1f1f1f;
  padding: 1rem;
  max-width: 10rem;
  position:absolute;
  right: 0;
  top: -0.5rem;  
  font-family: 'Montserrat';
}

.header-modal-items:hover {
  cursor: pointer;  
  color: #fff;
}

.popUp-modal {
  border-radius: 2rem;
  padding: 2rem 1rem;
  height: fit-content;
  width: fit-content;
  min-height: 15rem;
  // min-width: 21.25rem;
  background-color: #ccc;
  color: #1f1f1f;
  filter: drop-shadow(2px 4px 6px black);
  font-family: 'Montserrat';
  font-size: 1.5rem;
  text-align: center;  
  display: flex;
  // justify-content: center;
  align-items: center; 
  z-index: 9999  ; 
}

.close-modal-btn {
  background-color: transparent;
  border: none;
  position: absolute;
  top: 0;
  padding: 1rem;
  right: 0;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.3s;
}

.close-modal-btn:hover {
  color: #5e2552;
}
 
.modal-input {
  font-family: 'Roboto Mono';
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #000;
  font-size: 1rem;
  margin: 1rem 0;
}

.modal-input:focus {
  outline: none
}

.modal-submit {
  border-radius: 3rem;
  background-color: #7fa0ac;
  height: 3rem;
  width: fit-content;
  cursor: pointer;
  transition: 0.3s;
  font-weight: bold;
  border: none;
  color: #1f1f1f;
  font-family: 'Montserrat';
  padding: 1rem;
  font-size: 0.75rem;
}

.modal-submit:hover {
  color: #fff;
  background-color: #384a66;  
}
 
 
